<template>
  <Static>
    <div class="facebook background">
      <v-card flat>
        <Header />
      </v-card>
      <v-subheader class="bg-color white--text">
        <span class="ml-4">School Facebook Page</span>
      </v-subheader>

      <div class="fb-flex">
        <div class="fb-cont">
          <div v-if="loading" class="mt-4">
            <v-skeleton-loader
              class="mx-auto"
              width="320"
              type="list-item-avatar, divider, list-item-two-line, list-item-three-line, list-item-avatar, divider, list-item-two-line"
            ></v-skeleton-loader>
          </div>
          <div
            class="fb-page"
            data-href="https://www.facebook.com/techmanifest/"
            data-tabs="timeline,events,messages"
            data-width="500"
            data-height="650"
            data-small-header="false"
            data-adapt-container-width="true"
            data-hide-cover="false"
            data-show-facepile="true"
          ></div>
        </div>
      </div>
    </div>
  </Static>
</template>
<script>
import Static from '../layouts/Static'
import Header from '@/components/Header'
export default {
  name: 'Facebook',
  components: { Header, Static },
  data() {
    return {
      loading: true,
    }
  },
  mounted() {
    this.showToggle()
    this.init()
  },
  methods: {
    init() {
      if (window.FB) {
        window.FB.init({
          appId: 'your-app-id',
          autoLogAppEvents: true,
          xfbml: true,
          version: 'v10.0',
        })
      }
    },
    showToggle() {
      setTimeout(() => {
        this.loading = false
      }, 1700)
    },
  },
}
</script>
